//                   <option value="0" ng-disabled="true">Seleccionar Periodo</option>
//                   <option ng-repeat="periodo in Periodos" value="{{periodo}}">{{periodo.NOMBRE}}</option>
//                 </select>
//               </div>

//  html: `<input id="my-input" value="${
//           this.name
//         }" placeholder="Name" class="swal2-input">
//           <select id="my-select" value="${
//             this.parent
//           }" name="parent" class="form-control">
//             <option value="">nothing</option>
//             ${this.categories.map(
//               cat => `<option value="${cat.id}">${cat.name}</option>`
//             )}
//           </select>`,
</script>
<style scoped>
.coloriconos{
  color:white;
}
</style>
