                      <td class="text-center">{{ se.nombre_departamento }}</td>
                      <td class="text-center">{{ se.nombre_municipio }}</td>
                         <td class="text-center">
                        <a type="button" @click="actualizarestado(se.id_sede)" class="btn btn-sm btn-vimeo btn-icon-only" style="margin-right: 4px;">
                          <i class="fas fa-edit"></i>
                        </a>
                        <a type="button" @click="actualizarestado(se.id_sede)" class="btn btn-sm btn-danger btn-icon-only" style="margin-right: 4px;">
                          <i class="ni ni-ui-04"></i>
                        </a>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              </div>
              </div>
              <div class="text-center col-md-4">
                  <div class="col-md-12">
                  <h6><strong>Nombre Sede</strong></h6>
                  <div class="form-group">
                    <input
                      type="text"
                      placeholder="Regular"
                      v-model="nombre_sede"
                      class="form-control"
                    />
                  </div>
                </div>
                <div class="col-md-12">
                  <h6><strong>Departamento</strong></h6>
                  <div class="form-group">
                    <select
                      class="form-select"
                      v-model="departamento"
                      @change="getmunicipios()"
                    >
                      <option value="" disabled>
                        Seleccionar Departamento
                      </option>
                      <option
                        v-for="(item, index) in departamentos"
                        :value="item.id_departamento"
                        :key="index"
                      >
                        {{ item.nombre_departamento }}
                      </option>
                    </select>
                  </div>
                </div>
                <div class="col-md-12">
                  <h6><strong>Municipio</strong></h6>
                  <div class="form-group">
                    <select
                      class="form-select"
                      v-model="municipio_sede"
                    >
                      <option value="" disabled>Seleccionar Municipio</option>
                      <option
                        v-for="item in municipios"
                        :value="item.id_municipio"
                        :key="item.id_municipio"
                      >
                        {{ item.nombre_municipio }}
                      </option>
                    </select>
                  </div>
                </div>
                  <div class="text-center">
              <button type="button" class="btn btn-primary"
              @click="Agregarsede()">
                  <span>Guardar</span>
                </button>
              </div>
              </div>

              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
      </div>
    </div>
  </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import axios from "axios";

export default {
  name: "profile",
  data() {
    return {
       seleccionartap1:true,
       seleccionartap2:false,
       seleccionartap3:false,
       verformulario:false,
      vertabla:true,
      servicios:"",
      servi:"",
      modulos:"",
      modul:"",
      departamentos:"",
      municipios:"",
      sedes:"",
      sedes_totales:"",
      // ===================
      nombre_servicio:"",
      color_servicio:"",
      icono_servicio:"",
      inicial_servicio:"",
      departamento:"",
      municipio:"",
      sede:"",
      // ===================
      nombre_sede:"",
      municipio_sede:""

    };
  },
  mounted() {
    this.seleccionartap(1);
  },
   created() {
    this.servicios_activos();
    this.modulos_activos();
    this.sedes_activas_totales();
    this.getdepartamentos();
  },
    methods: {
      seleccionartap(opcion) {
            switch (opcion) {
              case 1:
              this.seleccionartap1 = true;
              this.seleccionartap2 = false;
              this.seleccionartap3 = false;
              break;
              case 2:
              this.seleccionartap1 = false;
              this.seleccionartap2 = true;
              this.seleccionartap3 = false;
              break;
              case 3:
              this.seleccionartap1 = false;
              this.seleccionartap2 = false;
              this.seleccionartap3 = true;
              break;
              default:
            }
          },
          sedes_activas() {
      const peticion = {
          departamento:this.departamento,
          municipio:this.municipio
        };
        axios.post("llamar_sedes",peticion)
          .then((respuesta) => {
            this.sedes = respuesta.data;
          }).catch((error) => {
            if(error.response.status == 400){
               this.$swal.fire(
                "Ups!!",
                "Token Invalido Cierre Session e Ingrese de Nuevo",
                "error"
              );
            }
          });
    },
      getdepartamentos() {
      axios.post("llamardepartamentos")
      .then((response) => {
          this.departamentos = response.data;
       }).catch((error) => {
            if(error.response.status == 400){
               this.$swal.fire(
                "Ups!!",
                "Token Invalido Cierre Session e Ingrese de Nuevo",
                "error"
              );
            }
          });
    },
    getmunicipios(){
      const tokenpeticion = {
        id_departamento: this.departamento,
      };
      axios.post("llamarmunicipios",tokenpeticion )
      .then((response) => {
          this.municipios = response.data;
         }).catch((error) => {
            if(error.response.status == 400){
               this.$swal.fire(
                "Ups!!",
                "Token Invalido Cierre Session e Ingrese de Nuevo",
                "error"
              );
            }
          });
    },
     servicios_activos() {
        axios.post("llamar_servicios_todos")
          .then((respuesta) => {
            this.servicios = respuesta.data;
          }).catch((error) => {
            if(error.response.status == 400){
               this.$swal.fire(
                "Ups!!",
                "Token Invalido Cierre Session e Ingrese de Nuevo",
                "error"
              );
            }
          });
    },
    modulos_activos() {
        axios.post("llamar_modulos")
          .then((respuesta) => {
            this.modulos = respuesta.data;
          }).catch((error) => {
            if(error.response.status == 400){
               this.$swal.fire(
                "Ups!!",
                "Token Invalido Cierre Session e Ingrese de Nuevo",
                "error"
              );
            }
          });
    },
    sedes_activas_totales() {
        axios.post("llamar_sedes_totales")
          .then((respuesta) => {
            this.sedes_totales = respuesta.data;
          }).catch((error) => {
            if(error.response.status == 400){
               this.$swal.fire(
                "Ups!!",
                "Token Invalido Cierre Session e Ingrese de Nuevo",
                "error"
              );
            }
          });
    },
    actualizarestado(usuario) {
this.$swal.fire({
            title: "Actualizar Estado",
            text: "Esta seguro de Cambiar el estado!",
            icon: "info",
            showDenyButton: false,
            showCancelButton: true,
            confirmButtonText: "Actualizar",
            cancelButtonColor: '#d33',
             html: `<div class="col-md-12">
                    <label class="form-control-label">Servicio</label>
                   <div class="form-group"> 
                   <select name="estado" id="estado" class="form-select">
                   <option value="">Seleccionar Servicio</option>
                   <option value="A">Activo</option>
                   <option value="I">Inactivo</option>
                    </select>
                    </div>
                    </div>`,
          })
          .then((result) => {
            if (result.isConfirmed) {
      const peticion = {
        usuario:usuario,
        estado: document.getElementsByName('estado')[0].value
      };
       if(peticion.estado == "" || peticion.estado == null || peticion.estado == undefined) {
        this.$swal.fire(
          "Intentar de nuevo",
          "Por favor Ingrese Una Contrasea",
          "info");
      } else {
      axios.post("actualizarestado",peticion)
        .then((respuesta) => {
          console.log(respuesta);
          if(respuesta.data.codigo == 0){
              this.$swal.fire("Exito", respuesta.data.mensaje, "success")
            }else{
              this.$swal.fire("Info", respuesta.data.mensaje, "error")
            }
        }).catch((error) => {
            if(error.response.status == 400){
               this.$swal.fire(
                "Ups!!",
                "Token Invalido Cierre Session e Ingrese de Nuevo",
                "error"
              );
            }
          });
            }
}});
    },
     vertablaservicios() {
      this.verformulario = false;
      this.vertabla = true;
    },
    ver_formulario() {
      this.verformulario = true;
      this.vertabla = false;
    },
    AgregarServicios() {
          this.$swal.fire({
            icon:"info",
            title: "Agregar Nuevo Servicio",
            text: "Esta seguro de Agregar este servicio!",
            showDenyButton: false,
            showCancelButton: true,
            confirmButtonText: "Agregar",
            cancelButtonColor: '#d33'
          })
          .then((result) => {
            if (result.isConfirmed) {
      const peticion = {
        nombre:this.nombre_servicio,
        color:this.color_servicio,
        icono:this.icono_servicio,
        inicial:this.inicial_servicio,
        departamento:this.departamento,
        municipio:this.municipio,
        sede:this.sede,
      };
      if(peticion.nombre == "" || peticion.nombre == null || peticion.nombre == undefined ||
      peticion.color == "" || peticion.color == null || peticion.color == undefined ||
      peticion.icono == "" || peticion.icono == null || peticion.icono == undefined ||
      peticion.inicial == "" || peticion.inicial == null || peticion.inicial == undefined ||
      peticion.departamento == "" || peticion.departamento == null || peticion.departamento == undefined ||
      peticion.municipio == "" || peticion.municipio == null || peticion.municipio == undefined ||
      peticion.sede == "" || peticion.sede == null || peticion.sede == undefined ) {
        this.$swal.fire(
          "Intentar de nuevo",
          "Por Diligenciar todos los datos",
          "info");
      } else {
        axios.post("agregar_servicios",peticion)
          .then((respuesta) => {
            if(respuesta.data.codigo == 0){
                this.$swal.fire("Exito", respuesta.data.mensaje, "success")
                this.servicios_activos();
              }else{
                this.$swal.fire("Info", respuesta.data.mensaje, "error")
              }
          }).catch((error) => {
              if(error.response.status == 400){
                 this.$swal.fire(
                  "Ups!!",
                  "Token Invalido Cierre Session e Ingrese de Nuevo",
                  "error"
                );
              }
            });
      }
}});
    },
    AgregarModulo() {
this.$swal.fire({
            title: "Agregar Nuevo Modulo",
            text: "Esta seguro de Cambiar la clave!",
            width: 1000,
            showDenyButton: false,
            showCancelButton: true,
            confirmButtonText: "Agregar",
            cancelButtonColor: '#d33',
             html: `<div class="row">
                    <div class="col-md-6">
                    <label class="form-control-label">Nombre Modulo</label>
                   <div class="form-group"> 
                  <input type="text"  placeholder="Escribir Nueva Contrasea" id="nombre_modulo" name="nombre_modulo"  class="form-control"/>
                    </div>
                    </div>
                    <div class="col-md-6">
                    <label class="form-control-label">Numero</label>
                   <div class="form-group"> 
                  <input type="text"  placeholder="Escribir Nueva Contrasea" id="codigo_modulo" name="codigo_modulo"  class="form-control"/>
                    </div>
                    </div>
                    </div>`,
          })
          .then((result) => {
            if (result.isConfirmed) {
      const peticion = {
        nombre: document.getElementsByName('nombre_modulo')[0].value,
        codigo: document.getElementsByName('codigo_modulo')[0].value
      };
      if(peticion.nombre == "" || peticion.nombre == null || peticion.nombre == undefined ||
      peticion.codigo == "" || peticion.codigo == null || peticion.codigo == undefined) {
        this.$swal.fire(
          "Intentar de nuevo",
          "Por Diligenciar todos los datos",
          "info");
      } else {
        axios.post("agregar_modulos",peticion)
          .then((respuesta) => {
            if(respuesta.data.codigo == 0){
                this.$swal.fire("Exito", respuesta.data.mensaje, "success")
                this.modulos_activos();
              }else{
                this.$swal.fire("Info", respuesta.data.mensaje, "error")
              }
          }).catch((error) => {
              if(error.response.status == 400){
                 this.$swal.fire(
                  "Ups!!",
                  "Token Invalido Cierre Session e Ingrese de Nuevo",
                  "error"
                );
              }
            });
      }
}});
    },
     Agregarsede() {
          this.$swal.fire({
            icon:"info",
            title: "Agregar Nueva Sede",
            text: "Esta seguro de Agregar esta Sede!",
            showDenyButton: false,
            showCancelButton: true,
            confirmButtonText: "Agregar",
            cancelButtonColor: '#d33'
          })
          .then((result) => {
            if (result.isConfirmed) {
      const peticion = {
        nombre:this.nombre_sede,
        departamento:this.departamento,
        municipio:this.municipio_sede
      };
      if(peticion.nombre == "" || peticion.nombre == null || peticion.nombre == undefined ||
      peticion.departamento == "" || peticion.departamento == null || peticion.departamento == undefined ||
      peticion.municipio == "" || peticion.municipio == null || peticion.municipio == undefined) {
        this.$swal.fire(
          "Intentar de nuevo",
          "Por Diligenciar todos los datos",
          "info");
      } else {
        axios.post("agregar_sedes",peticion)
          .then((respuesta) => {
            if(respuesta.data.codigo == 0){
                this.$swal.fire("Exito", respuesta.data.mensaje, "success")
                this.sedes_activas_totales();
              }else{
                this.$swal.fire("Info", respuesta.data.mensaje, "error")
              }
          }).catch((error) => {
              if(error.response.status == 400){
                 this.$swal.fire(
                  "Ups!!",
                  "Token Invalido Cierre Session e Ingrese de Nuevo",
                  "error"
                );
              }
            });
      }
}});
    },
  },
};

</script>
<style scoped>
.coloriconos{
  color:white;
}
</style>
